.about {
  &__title {

    margin-bottom: 32px;
    font-weight: 700;
    font-size: 32px;
    line-height: 32px;
  
    letter-spacing: -1.5px;
    color: $main-text;

    @include onTablet {
      font-size: 40px;
      line-height: 100%;
    }

    @include onDesktop {
      font-size: 48px;
      line-height: 48px;
    }
  }

  &__subtitle {
    margin-bottom: 16px;
    font-weight: 700;
    font-size: 16px;
    line-height: 140%;

    color: $main-text;

    &--last {
      margin-bottom: 40px;
    }
  }

  &__paragraph {
    font-weight: 400;
    font-size: 16px;
    line-height: 27px;

    color: $main-text;
  }
}
