.page {
  font-family: 'Inter', sans-serif;
  scroll-behavior: smooth;

  &__body {
    margin: 0;

    &--with-menu {
      overflow: hidden;
    }
  }

  &__menu {
    position: fixed;
    top: 0;
    left: 0;
    right: 0;

    transform: translateX(-100%);
    transition: transform 0.3s;

    &:target {
      transform: translateX(0);
    }
  }

  &__section {
    margin-bottom: 120px;
  }

  &__section-title {
    margin-bottom: 48px;
    font-weight: 700;
    font-size: 32px;
    line-height: 32px;
    color: $main-text;

    text-align: center;
    letter-spacing: -1.5px;

    @include onTablet {
      font-size: 40px;
      line-height: 40px;

      margin-bottom: 64px;
    }

    @include onDesktop {
      font-size: 48px;
      line-height: 48px;

      margin-bottom: 64px;
    }
  }
}
